.container{
    height: auto;
    margin: 0%;
    padding: 0px;
}
img{
    margin: 0px;
}
.heading-container{
    margin: 30px auto 30px 30px;
}
.headingTxt{
    font-weight: bolder;
    margin-bottom: 20px;
}
 .pTxt{
    margin-bottom: 15px;
    font-weight: 500;
}
.item{
    display: flex;
    margin: 0px 10px 10px 0px;
    font-weight: 500;
}
 i.fa-lg{
    margin-right: 20px;
    font-size: large;
}
form{
    display: flex;
    flex-direction: column;
    margin: 0px 30px;
}
.email{
    display: flex;
    justify-content: space-between;
}
.email-address{
    font-weight: bolder;
    font-size: small;
    margin-bottom: 5px;
}
.email-error{
        color: #ff0000;
        margin-bottom: 5px;
        font-size: small;
        font-weight: bold;
}
input[type="text"]{
    margin-bottom: 20px;
    height: 50px;
    border-radius: 5px;
    padding-left: 20px;
    border: 0.5px solid rgb(177, 169, 169);
}
button[type="submit"]{
    height: 55px;
    font-weight: 500;
    cursor: pointer;
}
.successMsg{
    position: relative;
    bottom: 700px;
    width: 300px;
    height: 200px;
    margin-left: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
i.fa-2xl{
    margin-bottom: 40px;
}
.h1{
    font-size: xx-large;
    font-weight: bolder;
    margin-right: 100px;
    margin-bottom: 20px;
}
.p{
    font-weight: 500;
}
.dismiss-btn{
    margin-top: 200px;
    cursor: pointer;
}
.dismiss-btn:hover{
    background-color: rgb(15, 145, 206);
}

@media (max-width: 375px){
    body{
        width: 375px;
    }
    .container{
        width: 375px;
    }
    img{
        width: 375px;
    }

}

@media (min-width:376px) and (max-width: 414px){
    body{
        width: 414px;
    }
    .container{
    width: 414px;
    }
    img{
    width: 414px;
    }

}

@media (min-width:415px) and (max-width: 514px){
    body{
        width:500px;
    }
    .container{
        width:500px;
    }
    img{
        width:500px;
    }
   
}

@media (min-width:515px) and (max-width: 600px){
    body{
        width:600px;
    }
    .container{
        width:600px;
    }
    img{
        width:600px;
    }
   
} 

@media (min-width:601px) and (max-width:1440px){
    body{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100vw;
        height: 100vh;
        background-color: rgb(38, 18, 58);
    }
    .container{
        
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 43vw;
        border-radius: 15px;
    }
    .secondary-container{
        display: flex;
        flex-direction: row-reverse;
        justify-content: center;
        position: fixed;
        z-index: 2;
        background-color: rgb(255, 255, 255);
        width: 550px;
        height: 400px;
        border-radius: 15px;
    }
    img{
        width: 250px;
        height: 23em;
        margin-top: 15px;
        margin-right: 15px;
    }
    .heading-container{
        width: 250px;
        margin: 35px 0px 0px 20px;
    }
    .headingTxt{
        padding-bottom: 10px;
        margin-bottom: 0px;
        font-size: xx-large;
    }
    .pTxt{
        margin-bottom: 20px;
        font-weight: 500;
        font-size: smaller;
    }
    i.fa-lg{
        margin-right: 10px;
        font-size: medium;
    }
    .item{
        margin: 0px;
        font-weight: 600;
        font-size: small;
    }
    .form-container{
        margin-top: 25px;
        width: 280px;
    }
    .email-address{
        font-weight: 800;
        font-size: x-small;
        margin-bottom: 5px;
    }
    .email-error{
        font-size: x-small;
        font-weight: 800;
    }
    input[type="text"]{
        margin-bottom: 8px;
        height: 30px;
        font-size: large;
    }
    button[type="submit"]{
        height: 35px;
        font-size: small;
    }
    
    .successMsg{
        position: relative;
        bottom: 1em;
        left: 0.3em;
        width: 330px;
        height: 330px;
        border-radius: 15px;
        background-color: #ffffff;
        display: flex;
        flex-direction: column;
        justify-content: center;
        z-index: 1;
        
    }
    i.fa-2xl{
        margin-bottom: 30px;
        margin-left: 30px;
    }
    .h1{
        font-size: xx-large;
        font-weight: bolder;
        margin-bottom: 20px;
        margin-left: 30px;
    }
    .p{
        font-weight: 500;
        font-size: smaller;
        width: 260px;
        margin-left: 30px;
    }
    .dismiss-btn{
        margin-top: 10px;
        width: 270px;
        margin-left: 30px;
    }
    
}